import React, { useEffect, useState } from "react";
import { LogOut, Home, ArrowRight, CheckCircle } from "lucide-react";
import { motion } from "framer-motion";
import "../../styles/auth.css";
import { useLanguage } from "../../i18n/LanguageContext";
import { UniversalLink as Link } from "../../database/UniversalLink";
import { useUniversalRouter } from "../../database/routerViteGatsbyUniversal";

export function Logout() {
  const { go } = useUniversalRouter();
  const { t } = useLanguage();
  const [isLoading, setIsLoading] = useState(true);
  const [isLoggedOut, setIsLoggedOut] = useState(false);
  const [progress, setProgress] = useState(0);

  // Simulate logout process
  useEffect(() => {
    const handleLogout = async () => {
      try {
        // Clear session/auth data
        if (typeof window !== "undefined") {
          localStorage.removeItem("authToken");
          localStorage.removeItem("userSession");
          localStorage.removeItem("has_onboarded");
          sessionStorage.clear();
          
          // Clear any auth cookies if they exist
          document.cookie.split(";").forEach((c) => {
            document.cookie = c
              .replace(/^ +/, "")
              .replace(/=.*/, `=;expires=${new Date().toUTCString()};path=/`);
          });
        }

        // Simulate logout process
        for (let i = 0; i <= 100; i += 20) {
          setProgress(i);
          await new Promise((resolve) => setTimeout(resolve, 150));
        }

        setProgress(100);
        setIsLoggedOut(true);
        setIsLoading(false);

        // Redirect after 3 seconds
        const timer = setTimeout(() => {
          go("/");
        }, 3000);

        return () => clearTimeout(timer);
      } catch (err) {
        console.error("Logout error:", err);
        setIsLoading(false);
        setIsLoggedOut(false);
      }
    };

    handleLogout();
  }, [go]);

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.1,
        delayChildren: 0.3,
      },
    },
  };

  const itemVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.6, ease: [0.21, 0.47, 0.32, 0.98] as [number, number, number, number] },
    },
  };

  return (
    <div className="auth-container logout-container">
      <div className="auth-bg">
        <div className="auth-gradient-orb orb-1"></div>
        <div className="auth-gradient-orb orb-2"></div>
        <div className="auth-gradient-orb orb-3"></div>
      </div>

      <motion.div
        className="auth-card logout-card"
        variants={containerVariants}
        initial="hidden"
        animate="visible"
      >
        {/* LOGOUT ICON */}
        <motion.div className="logout-icon-container" variants={itemVariants}>
          {isLoggedOut ? (
            <motion.div
              className="logout-success-icon"
              initial={{ scale: 0, rotate: -180 }}
              animate={{ scale: 1, rotate: 0 }}
              transition={{
                type: "spring",
                stiffness: 100,
                damping: 15,
              }}
            >
              <CheckCircle size={64} color="#10b981" strokeWidth={1.5} />
            </motion.div>
          ) : (
            <motion.div
              className="logout-processing-icon"
              animate={{ rotate: 360 }}
              transition={{ duration: 2, repeat: Infinity, ease: "linear" }}
            >
              <LogOut size={64} />
            </motion.div>
          )}
        </motion.div>

        {/* TITLE */}
        <motion.h1 className="logout-title" variants={itemVariants}>
          {isLoggedOut ? "You're Logged Out" : "Logging Out..."}
        </motion.h1>

        {/* DESCRIPTION */}
        <motion.p className="logout-description" variants={itemVariants}>
          {isLoggedOut
            ? "Your session has been securely closed. Thank you for using our service!"
            : "We're securely ending your session..."}
        </motion.p>

        {/* PROGRESS BAR */}
        {isLoading && (
          <motion.div className="logout-progress-container" variants={itemVariants}>
            <div className="logout-progress-bar">
              <motion.div
                className="logout-progress-fill"
                initial={{ width: 0 }}
                animate={{ width: `${progress}%` }}
                transition={{ duration: 0.3 }}
              />
            </div>
            <span className="logout-progress-text">{progress}%</span>
          </motion.div>
        )}

        {/* SUCCESS MESSAGE */}
        {isLoggedOut && (
          <motion.div className="logout-success-message" variants={itemVariants}>
            <div className="logout-checkmark">✓</div>
            <p className="logout-success-text">
              Session cleared • Cookies removed • All data secure
            </p>
          </motion.div>
        )}

        {/* ACTION BUTTONS */}
        <motion.div className="logout-actions" variants={itemVariants}>
          <Link to="/" className="logout-button primary-button">
            <Home size={20} />
            <span>Back to Home</span>
            <ArrowRight size={18} style={{ marginLeft: "8px" }} />
          </Link>
          <Link to="/login" className="logout-button secondary-button">
            <span>Sign In Again</span>
          </Link>
        </motion.div>

        {/* HINT TEXT */}
        {isLoggedOut && (
          <motion.p className="logout-hint" variants={itemVariants}>
            Redirecting to home in 3 seconds...
          </motion.p>
        )}
      </motion.div>

      <style >{`
        .logout-container {
          display: flex;
          align-items: center;
          justify-content: center;
          min-height: 100vh;
        }

        .logout-card {
          position: relative;
          z-index: 10;
          max-width: 500px;
          width: 100%;
          margin: 40px;
        }

        /* ICON CONTAINER */
        .logout-icon-container {
          display: flex;
          justify-content: center;
          margin-bottom: 32px;
        }

        .logout-processing-icon {
          color: #3b82f6;
          opacity: 0.8;
        }

        .logout-success-icon {
          filter: drop-shadow(0 0 20px rgba(16, 185, 129, 0.4));
        }

        /* TITLE */
        .logout-title {
          font-size: 2rem;
          font-weight: 700;
          font-family: "Outfit", sans-serif;
          letter-spacing: -0.02em;
          color: #f8fafc;
          margin: 0 0 16px 0;
          text-align: center;
          line-height: 1.2;
        }

        /* DESCRIPTION */
        .logout-description {
          font-size: 1rem;
          color: #94a3b8;
          text-align: center;
          margin: 0 0 32px 0;
          line-height: 1.6;
        }

        /* PROGRESS BAR */
        .logout-progress-container {
          display: flex;
          flex-direction: column;
          gap: 12px;
          margin-bottom: 32px;
        }

        .logout-progress-bar {
          width: 100%;
          height: 6px;
          background: rgba(255, 255, 255, 0.1);
          border-radius: 100px;
          overflow: hidden;
          border: 1px solid rgba(59, 130, 246, 0.2);
        }

        .logout-progress-fill {
          height: 100%;
          background: linear-gradient(90deg, #3b82f6, #60a5fa);
          box-shadow: 0 0 10px rgba(59, 130, 246, 0.5);
        }

        .logout-progress-text {
          font-size: 0.85rem;
          color: #60a5fa;
          font-weight: 600;
          text-align: right;
        }

        /* SUCCESS MESSAGE */
        .logout-success-message {
          background: rgba(16, 185, 129, 0.1);
          border: 1px solid rgba(16, 185, 129, 0.3);
          border-radius: 12px;
          padding: 16px;
          margin-bottom: 32px;
          display: flex;
          align-items: center;
          gap: 12px;
        }

        .logout-checkmark {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 32px;
          height: 32px;
          background: rgba(16, 185, 129, 0.2);
          border-radius: 50%;
          color: #10b981;
          font-size: 1.5rem;
          flex-shrink: 0;
        }

        .logout-success-text {
          font-size: 0.9rem;
          color: #6ee7b7;
          margin: 0;
          line-height: 1.4;
        }

        /* ACTIONS */
        .logout-actions {
          display: flex;
          flex-direction: column;
          gap: 12px;
          margin-bottom: 24px;
        }

        .logout-button {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          gap: 12px;
          padding: 14px 28px;
          border-radius: 8px;
          font-size: 1rem;
          font-weight: 600;
          text-decoration: none;
          transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
          cursor: pointer;
          border: 1px solid transparent;
          width: 100%;
        }

        .logout-button.primary-button {
          background: linear-gradient(135deg, #1e40af, #3b82f6);
          color: #fff;
          border: 1px solid rgba(59, 130, 246, 0.5);
          box-shadow: 0 8px 24px rgba(30, 64, 175, 0.3);
        }

        .logout-button.primary-button:hover {
          transform: translateY(-3px);
          box-shadow: 0 12px 32px rgba(30, 64, 175, 0.4);
          border-color: rgba(59, 130, 246, 0.8);
        }

        .logout-button.secondary-button {
          background: rgba(255, 255, 255, 0.05);
          color: #f8fafc;
          border: 1px solid rgba(255, 255, 255, 0.15);
          backdrop-filter: blur(10px);
        }

        .logout-button.secondary-button:hover {
          background: rgba(255, 255, 255, 0.1);
          border-color: rgba(255, 255, 255, 0.3);
          transform: translateY(-3px);
        }

        /* HINT TEXT */
        .logout-hint {
          font-size: 0.85rem;
          color: #64748b;
          text-align: center;
          margin: 0;
          font-weight: 500;
        }

        /* RESPONSIVE */
        @media (max-width: 768px) {
          .logout-title {
            font-size: 1.5rem;
          }

          .logout-description {
            font-size: 0.95rem;
          }

          .logout-card {
            margin: 20px;
          }

          .logout-button {
            padding: 12px 24px;
            font-size: 0.95rem;
          }
        }
      `}</style>
    </div>
  );
}
